
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'

import Drive from '@/tool/classFactory/drive.js';
import {getCookie} from '@/tool/common.js';
export default {
directives: {
TransferDom
},
components: {
Group,
Cell,
Confirm
},
data () {
return {
avartar: 'https://wx.qlogo.cn/mmopen/vi_32/DYAIOgq83eoLKnzzlSdMqQzIPhdxIZ2JyROWeajaMMib8Lq8PfmDnic3ou9oU7jJ8U21Esu4OQibRr04ccYy8THXA/132',
msg: 'Hello World!',
show: false,
title: '提示',
answer: '已经设置过性别,不允许修改性别',
showcancel: false,
cancel: '重听',
congfirm:'确定',
params:{
'sessionId':''
},
detailmsg:{
imgUrl:'',
name:''
},
userMsg:{
'avatar': '',
'nikename':''
}
}
},
mounted(){
const that =this;

this.clientHeight =   `${document.documentElement.clientHeight}`          
console.log(this.clientHeight)
that.changeFixed(this.clientHeight)
},
created () {
this.params.sessionId = getCookie('sessionId')
this.getUserMsg()
},
methods: {
getUserMsg(){
const that = this;
Drive.prototype.getUserMsg(that.params).then(res => {
let list = res.data.data || [];
if(list.length!=0){
that.userMsg = list
}
console.log(list)
});
},
changeFixed(clientHeight) {                        //动态修改样式
console.log(clientHeight);
this.$refs.homePage.style.height = clientHeight + 'px';

},
onPhoneTap(e){
const that = this
that.$router.push('../code')
},
onGenderTap(e){

const that = this
if(that.userMsg.sex){

}else {
that.$router.push('../cgender')
}

},
onNameTap(e){
const that = this
that.$router.push('../name')
},
whichChange(e) {
console.log('whichChange')
console.log(e)
},
handleAvatarSuccess(res) {
console.log('handleAvatarSuccess')
let item = this.detailmsg;
item.imgUrl = res.data;
console.log(item.imgUrl);
},

beforeAvatarUpload(file) {
console.log('beforeAvatarUpload')
const isLt2M = file.size / 1024 / 1024 < 1;
if (!isLt2M) {
this.$message.error('上传图片大小不能超过 1MB!');
}
return isLt2M;
},
onCancel () {
console.log('on cancel')
},
onConfirm (msg) {
console.log('on confirm')
if (msg) {
alert(msg)
}
},
onHide () {
console.log('on hide')
},
onShow () {
console.log('on show')
},
uploadHeadImg: function () {
console.log('uploadHeadImg')
this.$el.querySelector('.hiddenInput').click()
},

handleFile: function (e) {
console.log('handleFile')
const that = this
let $target = e.target || e.srcElement
let file = $target.files[0]
var reader = new FileReader()
reader.onload = (data) => {
let res = data.target || data.srcElement
that.userMsg.avatar = res.result

}
reader.readAsDataURL(file)
}
}
}



